import React, { useEffect, useCallback, useState, useMemo, useRef } from 'react'
import { CardBody, Image, Heading, RowType, Toggle, Text, Button, ArrowForwardIcon, Flex, Select, OptionProps, LogoIcon } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import styled from 'styled-components'

const Wrapper = styled(CardBody)`
  background: ${({ theme }) => theme.colors.backgroundAlt};
  border-radius: 10px;
  min-height: 480px;
  margin-bottom: 32px;
`

const SocialImage = styled.img`
  width: 25px;
  height: 25px;
  padding: 4px;
  border-radius: 15px;
  border: solid 1px white;
  margin-left: 5px;
`

const MarkImage = styled.img`
  margin: 10px;
  width: 280px;
`

const Space = styled.div`
  background: linear-gradient(180deg,#FFFFFF 0%,#000000 50%,#FFFFFF 100%);
  opacity: 0.4;
  width: 3px;
  height: 400px;
  margin: 0 30px;

  ${({ theme }) => theme.mediaQueries.xs} {
    width: 0px;
    height: 40px;
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    width: 3px;
    height: 400px;
  }
`

const OpenPresaleButton = styled(Button)`
  background: linear-gradient(90deg, #7080F4 0%, #FF55F8 102.14%);
  padding: 10px;
  border-radius: 10px;
  color: #ffffff;
  width: fit-content;
  margin-top: 5px;
`

const Content = styled(Flex)`
  flex-direction: row;

  ${({ theme }) => theme.mediaQueries.xs} {
    flex-direction: column;
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    flex-direction: row;
  }
`

const SocialContent = styled(Flex)`
  flex-direction: row;

  ${({ theme }) => theme.mediaQueries.xs} {
    flex-direction: column;
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    flex-direction: row;
    align-items: center;
  }
`


const IntroductionContent: React.FC = () => {
  const { t } = useTranslation()

  return (
    <>
      <Content mt="32px" justifyContent="space-between" mb="32px">
        <Heading scale="lg" color="text" mb="10px">
          {t('Featured Project')}
        </Heading>
        <SocialContent>
          <Heading scale="sm" color="text" mb="10px">
            {t('Follow Our Media Channels To Stay Updated')}
          </Heading>
          <Flex mb="10px">
            <SocialImage src="images/launch/telegram.svg" alt="telegram" />
            <SocialImage src="images/launch/twitter.svg" alt="twitter" />
            <SocialImage src="images/launch/youtube.svg" alt="youtube" />
          </Flex>
        </SocialContent>
      </Content>
      <Wrapper>
        <Content alignItems="center">
          <MarkImage src="images/launch/logo.png" alt="mark" />
          <Space />
          <Flex flexDirection="column">
            <Flex flexDirection="column">
              <Heading scale="md" mb="20px">
                {t('$Infinity PRE-SALE')}
              </Heading>
              <Text>
                {t('The Infinite Ecosystem will revolutionize DeFi by introducing the concept of Perpetual Leveled Farming. This system will enable the project to initiate innovations covering each aspects of DeFi through different Levels that will constitute the Infinite Ecosystem. All of this powered by $INFINITY.')}
              </Text>
            </Flex>
            <Flex flexDirection="column" mt="30px">
              <Text>
                {t('Raise Network:')}
              </Text>
              <Heading scale="md">
                {t('Binance Smart Chain')}
              </Heading>
            </Flex>
            <Flex flexDirection="column" mt="30px">
              <Text>
                {t('Project Network:')}
              </Text>
              <Heading scale="md">
                {t('Binance Smart Chain and Polygon')}
              </Heading>
            </Flex>
            <Flex flexDirection="column" mt="30px">
              <Text>
                {t('Target raise:')}
              </Text>
              <Heading scale="md">
                {t('250K')}
              </Heading>
            </Flex>
            <Flex flexDirection="column" mt="30px">
              <OpenPresaleButton as="a" target="_blank" href="http://presale.infinitefarm.tech/" >{t('Open Presale')}</OpenPresaleButton>
            </Flex>
          </Flex>
        </Content>
      </Wrapper>
    </>
  )
}

export default IntroductionContent